@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  margin: 0%;
  font-family: 'Roboto', sans-serif;
}

.brand {
  display: flex;
  align-items: flex-start;
  padding: 0px;
  max-height: 150px;
}
.navStyle {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 0px;
  margin-bottom: 30px;
  height: 15vh;
  background-size: cover;
  color: black;
  font-size: 20px;
  font-weight: bold;
}
.linkNav {
  list-style: none;
  margin: 30px 50px;
  color: black;
}
ul {
  display: flex;
  flex-direction: row;
  align-content: space-around;
  justify-content: flex-end;
}
input {
  border-radius: 10px;
  font-family: "Poppins", sans-serif;
  margin: 0px 10px;
}
.buttonSearch {
  background-color: black;
  color: white;
  border-radius: 10px;
  font-family: "Poppins", sans-serif;
  border: none;
}
a {
  text-decoration: none;
  color: rgb(74, 129, 74);
}
.linkNav:hover {
  color: black;
}

.containerIcon {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  color: black;
  font-size: 25px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
.count {
  font-size: 17px;
  padding: 0px 5px;
  margin: 2px;
}
.iconCart:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.containerItems {
  margin: 0px;
  width: 100%;
  text-align: center;
  align-content: center;
}

.itemCard {
  width: 400px;
  background-color: rgba(255, 255, 255, 0.815);
  margin: 5px 10px;
  padding-top: 15px;
  height: 500px;
  vertical-align: center;
  position: relative;
}
.imgCard {
  max-width: 250px;
  margin: 10px;
}
.speciesCard {
  font-weight: bold;
}
.descriptionCard {
  font-size: 13px;
  max-width: 350px;
}
.itemTitle {
  font-size: 15px;
  font-weight: 100;
  color: black;
}
.buttonDetail {
  border: none;
  background-color: black;
  color: white;
  padding: 10px;
}
.itemPrice {
  background-color: rgb(118, 199, 108);
  font-weight: 500;
  font-size: 15px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px;
  border-radius: 15px;
  position: absolute;
  right: 60px;
  top: 0px;
  font-weight: bold;
  color: black;
}

.containerItem {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.seguir-comprando {
    margin-left: 120px;
    margin-top: 0px;
    font-size: 15px;
    text-decoration: underline;
}
.buttonCounterUp,
.buttonCounterDown {
  border: none;
  background-color: white;
  font-size: 20px;
  font-weight: bold;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.buttonClear {
  background-color: black;
  border: none;
  color: white;
  width: 300px;
  padding: 10px;
}
.counterStyle {
  width: 20px;
  height: 20px;
  padding: 10px;
  background-color: white;
  text-align: center;
  font-weight: bold;
}
.containerCounter {
  margin: 30px 0px 30px 90px;
  margin-left: 90px;
  font-size: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-items: center;
}
.containerButtonsCounter {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 300px;
}

.detailContainer {
  background-color: white;
  padding: 20px;
  font-size: 15px;
}
.detailDescription {
  max-width: 300px;
  max-height: 450px;
}
.imgDetail {
  width: 350px;
  height: 450px;
}
.flexbox-item-detail {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.detailImage {
  align-content: center;
  width: 300px;
}
.cuidados-detail {
  font-size: 12px;
  background-color: rgb(214, 100, 100);
  border-radius: 10px;
  color: white;
  font-weight: 100;
  padding: 8px;
}
.detailCount {
  height: 450px;
}
.buttonFinish {
  background-color: black;
  border: none;
  color: white;
  width: 300px;
  padding: 10px;
  margin-left: 90px;
}

.img-carrito {
  max-width: 100px;
}
.table-cart {
  max-width: 90%;
}
td {
  width: 300px;
}
.eliminar-producto {
  border: none;
  background-color: white;
  color: red;
  text-decoration: underline;
}
.finalizar-compra {
  width: 300px;
  background-color: black;
  color: white;
  border: none;
  text-align: center;
  margin: auto;
  padding: 10px;
}
.tucarrito-title {
  margin-bottom: 40px;
}
.seguir-comprando {
  margin-left: 0px;
  border: none;
  background-color: white;
  text-decoration: underline;
  color: rgb(92, 92, 172);
  margin: 10px;
  font-size: 15px;
}
.containerCart {
  margin-top: 0px;
}

.no-items {
  font-size: 20px;
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 50%;
  margin: 10% auto;
}
.ver-productos {
  padding: 10px;
  background-color: black;
  color: white;
  border: none;
  width: 200px;
}
.container-cart-detail {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  max-width: 60%;
  margin: 10px auto;
  text-align: center;
}
.form {
  display: flex;
  flex-direction: column;
  height: 300px;
  width: 720px;
  background-color: rgb(152, 202, 152);
  padding: 40px;
  align-self: center;
  border-radius: 30px;
}
.x-target {
  right: 0px;
  top: 0px;
}
.button-submit {
  width: 300px;
  padding: 10px;
  background-color: black;
  border: none;
  color: white;
  margin-top: 10px;
}
#email,
#fname {
  width: 400px;
  margin: 20px 0px;
  border: none;
  border-radius: 3px;
  height: 30px;
  text-align: left;
  font-family: "Roboto", sans-serif;
  font-size: 15px;
}
.volver-inicio {
  border: none;
  background-color: white;
  text-decoration: underline;
  color: rgb(92, 92, 172);
  margin: 50px;
  font-size: 15px;
}

